  login() {
    this.authenticationService.login();
  }

  addQeuryParamForSearch(router: Router, route: ActivatedRoute, searchKey, zipcode) {
    // changes the route without moving from the current view or
    // triggering a navigation event,
    router.navigate(['/product/search-box/title'], {
        relativeTo: route,
        queryParams: {
          kw: this.searchKey,
          zip: this.zipcode,
          pr: 'l2h'
        },
        queryParamsHandling: 'merge',
        // preserve the existing query params in the route
        // skipLocationChange: true
        // do not trigger navigation
    });
  }

  addQeuryParamForSearchTheme(router: Router, route: ActivatedRoute, searchKey, zipcode) {
    // changes the route without moving from the current view or
    // triggering a navigation event,
    router.navigate(['/product/search/title'], {
        relativeTo: route,
        queryParams: {
          kw: searchKey,
          zip: '',
          pr: 'l2h'
        }
        // preserve the existing query params in the route
        // skipLocationChange: true
        // do not trigger navigation
    });
  }

getRetailers() {
  this.productService.getRetailersList().subscribe(res => {
    // console.log(res);
    this.retailers = res;
  })
}

openUnregisteredSearchModal(classNames) {
  this.unregisteredSearchModalRef = this.modalService.show(this.unregisteredSearch, {class: classNames});
}


  

  getCategoryData() {
    this.productService.getCategoryData().subscribe(res => {
      this.categories = res;
      //this.reorderedCategories.forEach(category => {
        //this.barChartLabels.push(category._id[0]);
        //this.barChartData[0].data.push(category.percentInStock);
      //})
    })
  }


  trackEvent(category) {
    if(window.dataLayer) {
      window.dataLayer.push({'CategoryName' : category});
    };
  }

  sendUpdateRequest() {
    this.support.supportType = SupportType.early_access;
    this.isProcessingUserUpdateRequest = true;
    this.supportService.sendEarlyAccessRequest(this.support).subscribe(res => {
      this.isUpdateRequestError = false;
      this.isProcessingUserUpdateRequest = false;
      // this.feedbackToggled = false;
      this.updateRequestFormSubmitted = true;
      // this.support = new Support();
      this.emailExists = false;
    },(error) => {
      this.isUpdateRequestError = true;
      this.isProcessingUserUpdateRequest = false;
      if(error.error.message == "email_exists") {
        this.emailExists = true;
      } else if(error.error.message == "invalid_email") {
        this.updateRequestErrorMessage = "Please provide a valid email address";
        this.emailExists = false;
      } else {
        this.updateRequestErrorMessage = "Oh no! Something went wrong, please try again in a while";
        this.emailExists = false;
      }
    })
  }

  sendUserSuggestions() {
    this.userSuggestionFormSubmitted = true;
    this.userSuggestionSupport.supportType = SupportType.product_suggestion;
    this.supportService.sendEarlyAccessRequest(this.userSuggestionSupport).subscribe(res => {
      // this.feedbackToggled = false;
      this.userSuggestionSupport = new Support();
    },(error) => {
      
    })
  }

